/** @format */
:root {
	--dark-blue-element: hsl(209, 23%, 22%);
	--very-dark-blue-background: hsl(207, 26%, 17%);
	--very-dark-blue-text: hsl(200, 15%, 8%);
	--dark-gray-input: hsl(0, 0%, 52%);
	--very-light-gray-background: hsl(0, 0%, 98%);
	--white-text-element: hsl(0, 0%, 98%);
}
* {
	box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	width: 100%;
	font-family: 'Nunito Sans', sans-serif;
	font-size: 1rem;
	background-color: hsl(207, 26%, 17%);
	background-color: var(--very-dark-blue-background);
}
.app-container {
	max-width: 100%;
}
.header {
	height: 50px;
}
.countryList-container {
	width: 90%;
	margin: auto;
}
@media screen and (max-width: 600px) {
	.countryList-container {
		width: 100%;
	}
}

/** @format */

ul {
	margin-left: auto;
	padding: 0;
	list-style: none;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	grid-gap: 2rem;
	gap: 2rem;
}
ul li {
	width: 200px;
	height: 300px;
	background-color: var(--dark-blue-element);
	border-radius: 5px;
	color: var(--white-text-element);
	cursor: pointer;
}
img {
	width: 100%;
	height: 50%;
}
h1 {
	--white: hsl(0, 0%, 100%);
	font-size: 1rem;
}
p {
	font-size: 14px;
	margin: 0;
}
h1,
p {
	margin-left: 1rem;
	text-align: left;
}
span {
	font-weight: 700;
}
@media screen and (max-width: 600px) {
	ul {
		flex-direction: column;
		align-items: center;
	}
	ul li {
		width: 250px;
		height: 350px;
	}
	img {
		min-width: 100%;
	}

	.country-list {
		width: 100%;
	}
}

/** @format */

section.search-container {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.search-input {
	padding: 0.5rem;
	border-radius: 5px;
	margin-left: 0;
	margin: 2rem 0 2rem 0;
	color: var(--dark-gray-input);
	background-color: var(--dark-blue-element);
	width: 290px;
	display: flex;
	justify-content: space-around;
}
.search-container .search-icon {
	color: var(--white-text-element);
	font-size: 1.5rem;
}
.search-container input {
	width: 230px;
	border: none;
	background: none;
	color: var(--dark-gray-input);
	outline: none;
	font-size: 0.9rem;
}
.search-container input::-webkit-input-placeholder {
	color: var(--white-text-element);
}
.search-container input:-ms-input-placeholder {
	color: var(--white-text-element);
}
.search-container input::placeholder {
	color: var(--white-text-element);
}

.filter-list {
	background-color: var(--dark-blue-element);
	color: var(--white-text-element);
	padding: 0.5rem;
}
@media screen and (max-width: 600px) {
	section.search-container {
		flex-direction: column;
		width: 90%;
		margin: auto;
	}
	.search-container input {
		width: 100%;
	}
	.search-input {
		box-sizing: border-box;
		width: 100%;
		/* padding: 0; */
		/* border: 2px solid rebeccapurple; */
	}
	.filter-list {
		justify-self: flex-start;
		align-self: flex-start;
	}
}

/** @format */

.country-container {
	width: 90%;
	margin: auto;
}
.back-btn {
	background-color: var(--dark-blue-element);
	color: var(--white-text-element);
	padding: 0.5rem 1.5rem;
	font-weight: 600;
	letter-spacing: 1px;
	border: none;
	margin: 2rem 0;
	cursor: pointer;
}
.country {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
article > img {
	width: 400px;
}
.country-info {
	color: var(--white-text-element);
	width: 50%;
}
.flex-info {
	display: flex;
	flex-direction: column;
	height: 120px;
	flex-wrap: wrap;
}
.flex-info p {
	padding: 2px;
}
.border-countries {
	margin-top: 2rem;
}
.border-btn {
	padding: 0.2rem 0.5rem;
	margin: 0 2px;
	background-color: var(--dark-blue-element);
	color: var(--white-text-element);
	border: none;
	cursor: pointer;
}
.title {
	font-size: 1.3rem;
}
@media screen and (max-width: 600px) {
	.country {
		flex-direction: column;
	}
	.country img {
		width: 100%;
	}
	.country-info {
		width: 100%;
	}
	.flex-info {
		width: 100%;
		height: auto;
	}
	.border-countries b {
		display: block;
		margin-bottom: 1rem;
	}
	.border-btn {
		padding: 0.5rem 1rem;
		margin-top: 5px;
	}
}

